import { useState } from "react";
import { useSelector, useDispatch } from "react-redux";

import { setFullName } from "../../../store/activateSlice";
import Card from "../../../components/shared/Card/Card";
import Button from "../../../components/shared/Button/Button";
import TextInput from "../../../components/shared/TextInput/TextInput";

const Index = ({ onClick }) => {
  const { fullName } = useSelector((state) => state.activate);
  const [name, setName] = useState(fullName);
  const dispatch = useDispatch();

  const onNext = () => {
    if (!name) return;

    dispatch(setFullName(name));
    onClick();
  };

  return (
    <div
      className="flex justify-content-center align-items-center"
      style={{ marginTop: "5rem" }}
    >
      <Card icon={"🤓"} text="What's your full name?">
        <TextInput
          value={name}
          type="text"
          onChange={(e) => setName(e.target.value)}
        />
        <p style={{ color: "#6d6969", margin: "20px 0" }}>
          Please enter your real name to join coders villa!
        </p>
        <Button onClick={onNext} title="Next" />
      </Card>
    </div>
  );
};

export default Index;
