import { Fragment, useState } from "react";

import PhoneEmail from "../Steps/PhoneEmail";
import Otp from "../Steps/Otp";

const steps = {
  1: PhoneEmail,
  2: Otp,
};

const Auth = () => {
  const [step, setStep] = useState(1);
  const Step = steps[step];
  const onNext = () => {
    setStep((prev) => {
      return prev + 1;
    });
  };
  return (
    <Fragment>
      <Step onClick={onNext} />
    </Fragment>
  );
};

export default Auth;
