import { useState } from "react";
import { useSelector, useDispatch } from "react-redux";

import Card from "../../../components/shared/Card/Card";
import Button from "../../../components/shared/Button/Button";
import TextInput from "../../../components/shared/TextInput/TextInput";
import { verifyOtp } from "../../../requests";
import { setAuth } from "../../../store/authSlice";
import { setError } from "../../../store/errorSlice";

const Index = ({ onClick }) => {
  const [otp, setOtp] = useState("");
  const { hash, phone } = useSelector((state) => state.auth.otp);
  const dispatch = useDispatch();
  console.log("Call");

  const sendOtp = async () => {
    if (!otp) {
      dispatch(setError("Please enter a OTP"));
      return;
    }

    if (otp.length > 10) {
      dispatch(setError("Please enter a valid OTP"));
      return;
    }
    const payload = {
      hash,
      otp,
      phone,
    };
    try {
      const { data } = await verifyOtp(payload);
      dispatch(setAuth(data));
      // onClick()
    } catch (error) {
      console.log(error);
    }
  };

  return (
    <div
      className="flex justify-content-center align-items-center"
      style={{ marginTop: "5rem" }}
    >
      <Card icon={"☎"} text="Enter Otp">
        <TextInput
          type="number"
          value={otp}
          onChange={(e) => setOtp(e.target.value)}
        />
        <div style={{ marginTop: "20px" }}>
          <Button onClick={sendOtp} title="Next" />
        </div>
      </Card>
    </div>
  );
};

export default Index;
