import { useState } from "react";
import Name from "../Steps/Name";
import Avatar from "../Steps/Avatar";

const steps = {
  1: Name,
  2: Avatar,
};

const Activate = () => {
  const [step, setSteps] = useState(1);
  const Step = steps[step];

  const onNext = () => {
    setSteps((prev) => prev + 1);
  };

  return (
    <>
      <Step onClick={onNext} />
    </>
  );
};

export default Activate;
