import axios from "axios";
// export const url = "https://codersvilla-backend.vercel.app";
const api = axios.create({
  baseURL: "/",
  withCredentials: true,
  headers: {
    "Content-Type": "application/json",
    Accept: "application/json",
  },
});

axios.defaults.withCredentials = true;

// list of all the end points
const refresh = async () => {
  await api.get(`/api/refresh`);
};
export const sendOtp = (data) => api.post("/api/send-otp", data);

export const verifyOtp = (data) => api.post("/api/verify-otp", data);

export const activate = async (data) => {
  await refresh();
  return api.post("/api/activate", data);
};

export const logout = async () => {
  await refresh();
  return api.get("/api/logout");
};

export const createRoom = async (data) => {
  await refresh();
  return api.post("/api/rooms", data);
};

export const getRooms = async () => {
  await refresh();
  return api.get("/api/rooms");
};

export const getRoom = async (id) => {
  await refresh();
  return api.get("/api/rooms/" + id);
};

