import { useState, useEffect } from "react";
import { useNavigate, useLocation } from "react-router-dom";
import { useSelector } from "react-redux";
import useWebRTC from "../../hooks/useWebRTC";
import { getRoom } from "../../requests";

const Room = () => {
  const location = useLocation();
  const navigate = useNavigate();
  const { user } = useSelector((state) => state.auth);
  const [room, setRoom] = useState({});
  const [isMute, setIsMute] = useState(true);
  const [userId, setUserId] = useState("");
  const roomId = location.pathname.split("/")[2];
  const { clients, provideRef, handleMute } = useWebRTC(roomId, user);
  const manualLeave = () => navigate("/rooms");

  useEffect(() => {
    getRoom(roomId).then((res) => setRoom(res.data.room));
  }, [roomId]);

  useEffect(() => {
    handleMute(isMute, userId);
  }, [isMute, userId]);

  return (
    <div>
      <div className="container">
        <button
          style={{
            border: "none",
            background: "none",
            padding: "12px 20px",
            color: "#fff",
            fontSize: "16px",
            cursor: "pointer",
          }}
          onClick={manualLeave}
        >
          &larr;
          <span
            style={{
              marginLeft: "4px",
              borderBottom: "2px solid #204ad5",
              paddingBottom: "4px",
            }}
          >
            All rooms
          </span>
        </button>
      </div>

      <div
        style={{
          background: "#302f2f",
          padding: "24px",
          margin: "12px",
          borderRadius: "12px",
          height: "80vh",
        }}
      >
        <div
          className=" flex justify-content-between align-items-center"
          style={{ marginBottom: "12px" }}
        >
          <h2 style={{ fontWeight: "bold", fontSize: "24px" }}>{room.topic}</h2>
          <div className="flex" style={{ gap: "4px" }}>
            <button
              style={{
                background: "#262626",
                color: "#fff",
                padding: "8px",
                border: "none",
                borderRadius: "4px",
                cursor: "pointer",
              }}
            >
              ✋
            </button>
            <button
              style={{
                background: "#262626",
                color: "#fff",
                padding: "8px",
                border: "none",
                borderRadius: "4px",
                cursor: "pointer",
              }}
              onClick={manualLeave}
            >
              ✌ Leave
            </button>
          </div>
        </div>
        <div className="flex" style={{ gap: "12px" }}>
          {clients.map((c) => {
            return (
              <div
                key={c._id}
                style={{
                  height: "70px",
                  width: "70px",
                  position: "relative",
                }}
              >
                <audio
                  ref={(instance) => provideRef(instance, c._id)}
                  // controls
                  autoPlay
                ></audio>
                <img
                  style={{
                    width: "100%",
                    height: "100%",
                    borderRadius: "50%",
                    border: "4px solid #205aff",
                  }}
                  src={c.avatar}
                  alt="user"
                />
                <h4 style={{ textAlign: "center" }}>{c.name.split(" ")[0]}</h4>
                <div
                  onClick={() => {
                    setIsMute((prev) => !prev);
                    if (c._id !== user._id) return;
                    setUserId(c._id);
                  }}
                  style={{
                    cursor: "pointer",
                    position: "absolute",
                    right: "0",
                    bottom: "0",
                  }}
                >
                  {!c.muted ? (
                    <div
                      style={{
                        fontSize: "16px",
                        background: "#20ff50",
                        color: "#fff",
                        borderRadius: "50%",
                        fontWeight: "bold",
                        padding: "4px",
                      }}
                    >
                      🎙
                    </div>
                  ) : (
                    <div
                      style={{
                        fontSize: "16px",
                        background: "#ff2020",
                        color: "#fff",
                        borderRadius: "50%",
                        fontWeight: "bold",
                        padding: "4px",
                      }}
                    >
                      🔇
                    </div>
                  )}{" "}
                </div>
              </div>
            );
          })}
        </div>
      </div>
    </div>
  );
};

export default Room;
