import styles from "./Card.module.css";

const Card = ({ icon = "😀", text = "Welcome To Coders", children }) => {
  return (
    <div className={styles.card}>
      <div className={styles.headingWrapper}>
        <span>{icon}</span>
        <h1 className={styles.heading}>{text}</h1>
      </div>
      {children}
    </div>
  );
};

export default Card;
