import { useState } from "react";
import { useDispatch } from "react-redux";

import Card from "../../../components/shared/Card/Card";
import Button from "../../../components/shared/Button/Button";
import Alert from "../../../components/UI/Alert";
import TextInput from "../../../components/shared/TextInput/TextInput";
import { sendOtp, verifyOtp } from "../../../requests/index";
import { setOtp } from "../../../store/authSlice";
import { setError } from "../../../store/errorSlice";

const btnStyles = {
  padding: "4px",
  fontSize: "1.5rem",
  outline: "none",
  borderRadius: "12px",
  cursor: "pointer",
  marginBottom: "4px",
};

const Phone = ({ onClick }) => {
  const [phone, setPhone] = useState("");
  const dispatch = useDispatch();

  const getOtp = async () => {
    if (!phone) {
      dispatch(setError("Please enter a phone number"));
      return;
    }

    if (phone.length > 10) {
      dispatch(setError("Please enter a valid phone number"));
      return;
    }
    const payload = {
      phone,
    };
    const { data } = await sendOtp(payload);
    dispatch(setOtp({ phone: data.phone, hash: data.hash }));
    onClick();
  };

  return (
    <Card icon={"☎"} text="Enter Your Phone Number">
      <TextInput
        type="number"
        value={phone}
        onChange={(e) => setPhone(e.target.value)}
      />
      <div style={{ marginTop: "30px" }}>
        <Button onClick={getOtp} title="Next" />
      </div>
      <p style={{ marginTop: "20px", color: "#817e7e" }}>
        By entering your number, you are agree on our terms and conditions and
        privacy policy!
      </p>
    </Card>
  );
};
const Email = ({ onClick }) => {
  const [email, setEmail] = useState("");

  return (
    <Card icon={"✉"} text="Enter Your Email">
      <TextInput
        type="email"
        value={email}
        onChange={(e) => setEmail(e.target.value)}
      />
      <div style={{ marginTop: "30px" }}>
        <Button onClick={onClick} title="Next" />
      </div>
      <p style={{ marginTop: "20px", color: "#817e7e" }}>
        By entering your email, you are agree on our terms and conditions and
        privacy policy!
      </p>
    </Card>
  );
};

const types = {
  phone: Phone,
  email: Email,
};

const Index = ({ onClick }) => {
  const [type, setType] = useState("phone");
  const Type = types[type];
  return (
    <div
      className="flex justify-content-center align-items-center"
      style={{ marginTop: "5rem" }}
    >
      <div
        style={{
          position: "relative",
          top: "-190px",
          left: "500px",
          zIndex: 100,
        }}
      >
        <button
          style={{
            color: "red",
            ...btnStyles,
            border: type === "phone" ? "2px solid #0077ff" : "none",
          }}
          onClick={() => setType("phone")}
        >
          ☎
        </button>
        <button
          style={{
            color: "blue",
            marginLeft: "4px",
            ...btnStyles,
            border: type === "email" ? "2px solid #0077ff" : "none",
          }}
          onClick={() => setType("email")}
        >
          ✉
        </button>
      </div>
      <Type onClick={onClick} />
    </div>
  );
};

export default Index;
