import styles from "./Button.module.css";

const Button = ({ title, onClick, style }) => {
  return (
    <button onClick={onClick} className={`${styles.btn}`} style={style}>
      <span>{title}</span>
      {title !== "X" && <span style={{ marginLeft: "10px" }}>&rarr;</span>}
    </button>
  );
};

export default Button;
