import { useState } from "react";
import { createRoom } from "../../requests";
import { useDispatch } from "react-redux";

import { setError } from "../../store/errorSlice";
import Button from "../shared/Button/Button";
import TextInput from "../shared/TextInput/TextInput";

const roomTypesStyle = {
  background: "#444141",
  borderRadius: "12px",
  display: "flex",
  flexDirection: "column",
  padding: "12px",
  alignItems: "center",
  cursor: "pointer",
};

const RoomModal = ({ closeModal }) => {
  const [type, setType] = useState("open");
  const [topic, setTopic] = useState("");

  const dispatch = useDispatch();

  const createRoomHandler = async () => {
    if (!topic) {
      dispatch(setError("Please provide topic"));
      return;
    }

    try {
      const payload = {
        type,
        topic,
      };
      await createRoom(payload);
      closeModal();
    } catch (error) {
      console.log(error);
    }
  };

  return (
    <div
      id="room-wrapper"
      style={{
        position: "fixed",
        top: "0",
        left: "0",
        right: 0,
        bottom: 0,
        display: "flex",
        justifyContent: "center",
        alignItems: "center",
        background: "rgba(0,0,0,0.6)",
        width: "100vw",
      }}
      onClick={(e) => {
        const wrapper = document.getElementById("room-wrapper");
        if (e.target === wrapper) {
          closeModal();
        }
      }}
    >
      <div
        style={{
          borderRadius: "16px",
          position: "relative",
          width: "50%",
          maxWidth: "400px",
          padding: "28px",
          background: "#444141",
        }}
      >
        <div style={{ position: "absolute", top: "0px", right: "0px" }}>
          <Button
            onClick={closeModal}
            title="X"
            style={{ background: "#cf4444" }}
          />
        </div>
        <div style={{ borderBottom: "1px solid #2f2f2f", margin: "10px 0" }}>
          <p style={{ paddingBottom: "4px" }}>
            Enter the topic to be discussed!
          </p>
          <TextInput
            type="text"
            style={{ width: "100%" }}
            value={topic}
            onChange={(e) => setTopic(e.target.value)}
          />
          <h3 style={{ margin: "10px 0" }}>Room Types</h3>
          <div
            className="flex justify-content-between"
            style={{ margin: "10px 0" }}
          >
            <div
              onClick={() => setType("open")}
              style={{
                ...roomTypesStyle,
                background: type === "open" && "#5e5858",
              }}
            >
              <div style={{ fontSize: "32px" }}>🌎</div>
              <p>Open</p>
            </div>
            <div
              onClick={() => setType("social")}
              style={{
                ...roomTypesStyle,
                background: type === "social" && "#5e5858",
              }}
            >
              <div style={{ fontSize: "32px" }}>🤼</div>
              <p>Social</p>
            </div>
            <div
              onClick={() => setType("private")}
              style={{
                ...roomTypesStyle,
                background: type === "private" && "#5e5858",
              }}
            >
              <div style={{ fontSize: "32px" }}>🔐</div>
              <p>Private</p>
            </div>
          </div>
        </div>

        <div
          className="flex justify-content-center"
          style={{ flexDirection: "column" }}
        >
          <p style={{ textAlign: "center" }}>Start a room, open to everyone</p>
          <button
            style={{
              background: "#20bd5b",
              border: "none",
              outline: "none",
              borderRadius: "50px",
              padding: "12px 20px",
              cursor: "pointer",
              marginTop: "4px",
            }}
            onClick={createRoomHandler}
          >
            <span>🎉</span>
            <span>Let's Go</span>
          </button>
        </div>
      </div>
    </div>
  );
};

export default RoomModal;
