import { useNavigate } from "react-router-dom";

const imgStyle = {
  borderRadius: "50%",
  border: "2px solid #20bd5b",
  height: "50px",
  width: "auto",
};

const RoomCard = ({ room }) => {
  const speaker1 = room.speakers[0];
  const speaker2 = room.speakers[1];

  const navigate = useNavigate();

  return (
    <div
      style={{
        background: "#2f2f2f",
        borderRadius: "16px",
        flexDirection: "column",
        padding: "14px",
        margin: "14px",
        cursor: "pointer",
      }}
      className="flex justify-content-center"
      onClick={() => navigate("/rooms/" + room._id)}
    >
      <h3 style={{ textAlign: "center" }}>{room.topic}</h3>
      <div
        className="flex justify-content-between"
        style={{ marginTop: "12px" }}
      >
        <div style={{ margin: "10px" }}>
          <img src={speaker1.avatar} alt="user 1" style={imgStyle} />
          {!!speaker2 && (
            <img
              style={{
                ...imgStyle,
                transform: "translate(-20px,10px)",
                border: "2px solid #4f20bd",
              }}
              src={speaker2.avatar}
              alt="user 2"
            />
          )}
        </div>
        <div
          className="flex justify-content-center"
          style={{ flexDirection: "column" }}
        >
          <p
            className="flex"
            style={{
              alignItems: "center",
              justifyContent: "center",
              gap: "8px",
            }}
          >
            <span>{speaker1.name}</span>
            <span style={{ fontSize: "24px" }}>☁</span>
          </p>
          {!!speaker2 && (
            <p
              className="flex"
              style={{
                alignItems: "center",
                justifyContent: "center",
                gap: "8px",
              }}
            >
              <span>{speaker2.name}</span>
              <span style={{ fontSize: "24px" }}>☁</span>
            </p>
          )}
        </div>
      </div>

      <div
        className=""
        style={{ marginLeft: "auto", fontSize: "18px", marginTop: "12px" }}
      >
        <span>{room.speakers.length}</span>
        <span>🧔</span>
      </div>
    </div>
  );
};

export default RoomCard;
