import { useState, useEffect } from "react";
import { getRooms } from "../../requests";
import RoomCard from "../../components/UI/RoomCard";
import RoomModal from "../../components/UI/RoomModal";
import Loader from "../../components/shared/Loader/Loader";
const Rooms = () => {
  const [showModal, setShowModal] = useState(false);
  const [loading, setLoading] = useState(true);
  const [rooms, setRooms] = useState([]);

  const getRoomsHandler = async () => {
    try {
      const { data } = await getRooms();
      setRooms(data.rooms);
    } catch (error) {
      console.log(error);
    } finally {
      setLoading(false);
    }
  };

  useEffect(() => {
    getRoomsHandler();
  }, []);

  if (loading) return <Loader />;

  return (
    <div className="container" style={{ marginTop: "10px" }}>
      <div className="flex justify-content-between">
        <div className="flex align-items-center" style={{ gap: "8px" }}>
          <span
            style={{
              color: "#2ca53c",
              fontWeight: "bold",
              fontSize: "16px",
              borderBottom: "2px solid #0077ff",
            }}
          >
            Voice Rooms
          </span>
          <div
            className="flex align-items-center"
            style={{
              background: "#2f2f2f",
              padding: "10px",
              color: "#fff",
              borderRadius: "12px",
            }}
          >
            <span>🔍</span>
            <input
              style={{
                color: "#fff",
                background: "transparent",
                width: "100%",
                outline: "none",
                border: "none",
                minWidth: "200px",
                paddingLeft: "10px",
              }}
              type="text"
            />
          </div>
        </div>

        <div className="">
          <button
            style={{
              background: "#20bd5f",
              padding: "10px 20px",
              borderRadius: "50px",
              cursor: "pointer",
            }}
            onClick={() => {
              setShowModal(true);
            }}
          >
            <span style={{ color: "#4e0ee2" }}>🗣</span>
            <span>Join Room</span>
          </button>
        </div>
      </div>

      <div className="flex" style={{ flexWrap: "wrap" }}>
        {rooms.map((room) => (
          <RoomCard key={room._id} room={room} />
        ))}
      </div>

      {showModal && <RoomModal closeModal={() => setShowModal(false)} />}
    </div>
  );
};

export default Rooms;
