import { useState } from "react";
import { useSelector, useDispatch } from "react-redux";

import { setAuth } from "../../../store/authSlice";
import { setAvatar } from "../../../store/activateSlice";
import Card from "../../../components/shared/Card/Card";
import Button from "../../../components/shared/Button/Button";
import { activate } from "../../../requests";
import Loader from "../../../components/shared/Loader/Loader";

const Index = ({ onClick }) => {
  const { fullName, avatar } = useSelector((state) => state.activate);
  const dispatch = useDispatch();
  const [img, setImage] = useState(avatar || "/assets/coder-animated");
  const [loading, setLoading] = useState(false);

  const captureImage = (e) => {
    const file = e.target.files[0];
    const reader = new FileReader();
    reader.readAsDataURL(file);
    reader.onloadend = function () {
      setImage(reader.result);
      dispatch(setAvatar(reader.result));
    };
  };

  const activateUser = async () => {
    const payload = {
      name: fullName,
      avatar: img,
    };
    try {
      setLoading(true);
      const { data } = await activate(payload);
      if (data.auth) {
        dispatch(setAuth(data));
      }
    } catch (err) {
      console.log(err);
    } finally {
      setLoading(false);
    }
    // onClick()
  };

  if (loading) return <Loader message="Activation in progress..." />;

  return (
    <div
      className="flex justify-content-center align-items-center"
      style={{ marginTop: "5rem" }}
    >
      <Card icon={"🧑"} text={`Hey! here you are ${fullName.split(" ")[0]}`}>
        <p style={{ color: "#6d6969", marginBottom: "10px" }}>
          Is this right avatar?
        </p>

        <div
          className="flex align-items-center justify-content-center"
          style={{
            border: "2px solid #0077ff",
            width: "110px",
            height: "110px",
            borderRadius: "50%",
            overflow: "hidden",
            margin: "0px 0 20px 0",
          }}
        >
          <img
            style={{ width: "100%", height: "100%", objectFit: "cover" }}
            src={img}
            alt="avatar"
          />
        </div>

        <div>
          <input
            onChange={captureImage}
            id="avatar"
            type="file"
            hidden
            accept="image/*"
          />
          <label
            htmlFor="avatar"
            style={{
              color: "#0077ff",
              cursor: "pointer",
              display: "inline-block",
              marginBottom: "10px",
            }}
          >
            Choose Your Avatar
          </label>
        </div>

        <Button onClick={activateUser} title="Next" />
      </Card>
    </div>
  );
};

export default Index;
