import { Link } from "react-router-dom";
import { useDispatch, useSelector } from "react-redux";

import { logout } from "../../../requests";
import Button from "../Button/Button";
import styles from "./Navigation.module.css";
import { setAuth } from "../../../store/authSlice";

const Navigation = () => {
  const dispatch = useDispatch();
  const { isAuth, user } = useSelector((state) => state.auth);
  const logoutHandler = async () => {
    try {
      const { data } = await logout();

      dispatch(setAuth(data));
    } catch (error) {
      console.log(error);
    }
  };
  return (
    <nav className={`container flex justify-content-between ${styles.navbar}`}>
      <Link
        to="/"
        style={{
          color: "#fff",
          textDecoration: "none",
          fontWeight: "bold",
          fontSize: "22px",
          display: "flex",
          alignItems: "center",
        }}
      >
        <span>👋</span>
        <span style={{ marginLeft: "10px" }}>Coders Villa</span>
      </Link>

      {isAuth && (
        <div style={{ gap: "8px" }} className="flex align-items-center ">
          <Link
            to="/profile"
            style={{
              textDecoration: "none",
              color: "white",
              display: "flex",
              alignItems: "center",
              gap: "8px",
            }}
          >
            <span>{user.name}</span>
            <img
              style={{
                borderRadius: "50%",
                objectFit: "cover",
                border: "2px solid #0077ff",
              }}
              src={user.avatar}
              height="50"
              width="50"
              alt="avatar"
            />
          </Link>
          <Button title="Logout" onClick={logoutHandler} />
        </div>
      )}
    </nav>
  );
};

export default Navigation;
