import React from "react";

const TextInput = (props) => {
  return (
    <div>
      <input
        {...props}
        style={{
          background: "#323232",
          border: "none",
          padding: "10px 20px",
          width: "200px",
          color: "#fff",
          fontSize: "16px",
          borderRadius: "10px",
          outline: "none",
          ...props.style,
        }}
      />
    </div>
  );
};

export default TextInput;
